import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { useTheme } from '@mui/material/styles';
import { styled } from '@mui/material/styles';

import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, IconButton, Typography, Button, TextField, TableContainer, TableHead, TableRow, Tooltip, Fab } from '@mui/material';
// project imports
import { useDispatch, useSelector } from 'store';
//Icons
import DeleteIcon from '@mui/icons-material/Delete'
// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';

// third-party
import { useFormik } from 'formik';
import EditAttributesSharp from '@mui/icons-material/Edit';

//redux
import { SaveInventoryData } from 'store/slices/inventoryteam';

const DivValue = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    paddingRight: 12,
    paddingTop: 2,
    fontSize: 16,
}));

const DivTitle = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.common.black,
    paddingTop: 2,
    paddingLeft: 12,
    fontSize: 16,
    fontWeight: 400
}));

export default function PaymentForm({ paymentData, handleNext, handleBack }) {

    const dispatch = useDispatch();

    const formik = useFormik({
        initialValues: {
            cardName: paymentData.cardName,
            cardNumber: paymentData.cardNumber
        },
        onSubmit: (values) => {
            SaveTableData();
            handleNext();
        }
    });
    const theme = useTheme();

    const inventorydataState = useSelector((state) => state.inventory);

    const [AllDataList, setAllDataList] = useState([]);
    const [editid, setEditId] = useState("");
    useEffect(() => {
        const filterdata = inventorydataState.inventorydata.filter((item) => {
            return Object.values(item).some((value) => value !== "");
        })
        setAllDataList(filterdata);
    }, [inventorydataState]);

    const handleInputChange = (id, column, value) => {
        const updatedData = AllDataList.map((row) =>
            row.id === id ? { ...row, [column]: value } : row
        );
        setAllDataList(updatedData);
    };

    const EditData = (id) => () => {
        setEditId(id);
    }

    const DeleteRowData = (id) => () => {
        setAllDataList(AllDataList.filter((row) => row.id != id));
    }

    const SaveTableData = async () => {
        await dispatch(SaveInventoryData(AllDataList));
    }

    return (
        <>
            <form onSubmit={formik.handleSubmit}>
                <Grid container alignItems="center" justifyContent="center" spacing={2}>
                    <Grid item sm={9}>
                        <Grid
                            container justifyContent="center" alignItems="center"
                            mb={4}
                        >
                            <Grid item xs={1}></Grid>
                            <Grid item xs={11}>
                                <Typography variant='h1'>Finished Goods Release From</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} sm={3}>
                        <Grid container mt={1} mb={1}>
                            <Grid item xs={8}>
                                <DivValue align="end" justifyContent="flex-end">Work Order NO : </DivValue>
                            </Grid>
                            <Grid item xs={4}>
                                <DivTitle>195583</DivTitle>
                            </Grid>
                            <Grid item xs={8}>
                                <DivValue align="end">Date : </DivValue>
                            </Grid>
                            <Grid item xs={4}>
                                <DivTitle>25/11 2023</DivTitle>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <TableContainer>
                            <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell align='center' colSpan={4}>Total Area</TableCell>
                                        <TableCell align='center' colSpan={5}></TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align='center'>Area</TableCell>
                                        <TableCell align='center'>Height</TableCell>
                                        <TableCell align='center'>Width</TableCell>
                                        <TableCell align='center'>Length</TableCell>
                                        <TableCell align='center'>Material Name</TableCell>
                                        <TableCell align='center'>Thickness</TableCell>
                                        <TableCell align='center'>Unit</TableCell>
                                        <TableCell align='center'>Quantity</TableCell>
                                        <TableCell align='center'>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {AllDataList.map((row) => (
                                        <>
                                            {row.id != editid && (
                                                <TableRow hover key={row.id}>
                                                    {Object.keys(row).map((item, i) => (
                                                        <>
                                                            {item != "id" && (
                                                                <TableCell align="center" key={i}>
                                                                    <TableCell align="center" key={i}>{row[item]}</TableCell>
                                                                </TableCell>
                                                            )}
                                                        </>
                                                    ))}
                                                    <TableCell align="center" sx={{ pr: 3 }}>
                                                        <div style={{ display: 'flex', justifyContent: 'center' }}>
                                                            <EditAttributesSharp
                                                                onClick={EditData(row.id)}
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500' }}
                                                            />
                                                            <DeleteIcon
                                                                onClick={DeleteRowData(row.id)}
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500', ml: 2 }}
                                                            />
                                                        </div>
                                                    </TableCell>
                                                </TableRow>
                                            )}
                                            {editid == row.id && (
                                                <TableRow hover key={row.id}>
                                                    {Object.keys(row).map((item, i) => (
                                                        <>
                                                            {item != "id" && (
                                                                <TableCell align="center" key={i}>
                                                                    <TextField
                                                                        value={row[item]}
                                                                        onChange={(e) => handleInputChange(row.id, item, e.target.value)}
                                                                        id="standard-basic" variant="standard"
                                                                    />
                                                                </TableCell>
                                                            )}
                                                        </>
                                                    ))}
                                                    <TableCell align="center" sx={{ pr: 3 }}>
                                                        <IconButton onClick={DeleteRowData(row.id)} size="large">
                                                            <DeleteIcon
                                                                fontSize="small"
                                                                aria-controls="menu-popular-card-1"
                                                                aria-haspopup="true"
                                                                sx={{ color: 'grey.500' }}
                                                            />
                                                        </IconButton>
                                                    </TableCell>
                                                </TableRow>
                                            )}
                                        </>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack direction="row" justifyContent="space-between">
                            <Button onClick={handleBack} sx={{ my: 3, ml: 1 }}>
                                Back
                            </Button>
                            <AnimateButton>
                                <Button variant="contained" type="submit" sx={{ my: 3, ml: 1 }}>
                                    Next
                                </Button>
                            </AnimateButton>
                        </Stack>
                    </Grid>
                </Grid>
            </form>
        </>
    );
}

PaymentForm.propTypes = {
    paymentData: PropTypes.object,
    setPaymentData: PropTypes.func,
    handleNext: PropTypes.func,
    handleBack: PropTypes.func,
    setErrorIndex: PropTypes.func
};
