import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';

import { styled } from '@mui/material/styles';

import { Grid, Table, TableBody, TableCell, Stack, tableCellClasses, IconButton, Typography, Button, TextField, TableContainer, TableHead, TableRow, } from '@mui/material';
// project imports
import { useDispatch, useSelector } from 'store';
//Icons
import DeleteIcon from '@mui/icons-material/Delete'
import { SaveInventoryData } from 'store/slices/inventoryteam';

// project imports
import AnimateButton from 'ui-component/extended/AnimateButton';

// third-party
import { useFormik } from 'formik';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        maxWidth: 50
    }
}));

const DivValue = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    paddingRight: 12,
    paddingTop: 2,
    fontSize: 16,
}));

const DivTitle = styled('div')(({ theme }) => ({
    ...theme.typography.button,
    backgroundColor: theme.palette.background.paper,
    color: theme.palette.mode === 'dark' ? theme.palette.common.white : theme.palette.common.black,
    paddingTop: 2,
    paddingLeft: 12,
    fontSize: 16,
    fontWeight: 400
}));

const StyledTableCellGroup = styled(TableCell)(({ theme }) => ({
    [`&.${tableCellClasses.head}`]: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white
    },
}));

// ==============================|| FORM WIZARD - VALIDATION  ||============================== //

const BasicInfo = ({ shippingData, setShippingData, handleNext }) => {
    const formik = useFormik({
        initialValues: {
            mainContractor: shippingData.mainContractor,
        },
        onSubmit: (values) => {
            SaveTableData();
            handleNext();
        }
    });
    const dispatch = useDispatch();

    const [AddtableData, setAddtable] = useState([]);

    const Addtablerow = () => {
        const newRow = { id: AddtableData.length + 1, Area: "", Height: "", Width: "", Length: "", Material: "", Thickness: "", Unit: "", Quantity: "" };
        setAddtable([...AddtableData, newRow]);
    }

    const handleInputChange = (id, column, value) => {
        const updatedData = AddtableData.map((row) =>
            row.id === id ? { ...row, [column]: value } : row
        );
        setAddtable(updatedData);
    };

    const DeleteRowData = (id) => () => {
        setAddtable(AddtableData.filter((row) => row.id != id));
    }

    const SaveTableData = async () => {
        await dispatch(SaveInventoryData(AddtableData));
    }

    return (
        <>
            <form onSubmit={formik.handleSubmit} id="validation-forms">
                <Grid container alignItems="center" justifyContent="center" spacing={2}>
                    <Grid item sm={9}>
                        <Grid
                            container justifyContent="center" alignItems="center"
                            mb={4}
                        >
                            <Grid item xs={1}></Grid>
                            <Grid item xs={11}>
                                <Typography variant='h1'>Finished Goods Release From</Typography>
                            </Grid>
                        </Grid>
                    </Grid>
                    <Grid item xs={12} sm={3}>
                        <Grid container mt={1} mb={1}>
                            <Grid item xs={8}>
                                <DivValue align="end" justifyContent="flex-end">Work Order NO : </DivValue>
                            </Grid>
                            <Grid item xs={4}>
                                <DivTitle>195583</DivTitle>
                            </Grid>
                            <Grid item xs={8}>
                                <DivValue align="end">Date : </DivValue>
                            </Grid>
                            <Grid item xs={4}>
                                <DivTitle>25/11 2023</DivTitle>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <Grid container spacing={3}>
                    <Grid item xs={12}>
                        <TableContainer>
                            <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell align='center' colSpan={4}>Total Area</TableCell>
                                        <TableCell align='center' colSpan={5}></TableCell>
                                    </TableRow>
                                    <TableRow>
                                        <TableCell align='center'>Area</TableCell>
                                        <TableCell align='center'>Height</TableCell>
                                        <TableCell align='center'>Width</TableCell>
                                        <TableCell align='center'>Length</TableCell>
                                        <TableCell align='center'>Material Name</TableCell>
                                        <TableCell align='center'>Thickness</TableCell>
                                        <TableCell align='center'>Unit</TableCell>
                                        <TableCell align='center'>Quantity</TableCell>
                                        <TableCell align='center'>Action</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>

                                    {AddtableData.map((row) => (
                                        <TableRow hover key={row.id}>
                                            {Object.keys(row).map((item, i) => (
                                                <>
                                                    {item != "id" && (
                                                        <TableCell align="center" key={i}>
                                                            <TextField
                                                                value={row[item]}
                                                                onChange={(e) => handleInputChange(row.id, item, e.target.value)}
                                                                id="standard-basic" variant="standard"
                                                            />
                                                        </TableCell>
                                                    )}
                                                </>
                                            ))}

                                            <TableCell align="center" sx={{ pr: 3 }}>
                                                <IconButton onClick={DeleteRowData(row.id)} size="large">
                                                    <DeleteIcon
                                                        fontSize="small"
                                                        aria-controls="menu-popular-card-1"
                                                        aria-haspopup="true"
                                                        sx={{ color: 'grey.500' }}
                                                    />
                                                </IconButton>
                                            </TableCell>

                                        </TableRow>
                                    ))}
                                </TableBody>
                            </Table>
                        </TableContainer>
                    </Grid>
                    <Grid item xs={12}>
                        <Stack direction="row" justifyContent="flex-end">
                            <AnimateButton>
                                <Button variant="contained" sx={{ my: 3, ml: 1 }} onClick={Addtablerow}>
                                    AddTable
                                </Button>
                                <Button variant="contained" sx={{ my: 3, ml: 1 }} type="submit">
                                    Next
                                </Button>
                            </AnimateButton>
                        </Stack>
                    </Grid>
                </Grid>
            </form>
        </>
    );
};

BasicInfo.propTypes = {
    shippingData: PropTypes.object,
    setShippingData: PropTypes.func,
    handleNext: PropTypes.func,
    setErrorIndex: PropTypes.func
};

export default BasicInfo;
